body, html, #root {
  height: 100%;
}

html{
  font-size: 10px;
}

h1{
  padding: 0.77rem 0;
  background: url(./pexels-pixabay-158664.jpg);
  text-align: center;
  font-family: 'montserrat', sans-serif;
  font-size: 1.88rem;
  color: #fff;
}

h1 .highlight {
  color: rgb(46, 113, 238);
}
a{
  text-decoration: none;
  color: #ffff;
 }

h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 1.55rem;
}



.App {
  height: 100%;
  padding:  0 17% 10% 17%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("./john-price-RAZQiZOX3mU-unsplash.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  color: #fff;
}

.App-playlist {
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 1020px) {
    .App-playlist {
      align-items: center;
      flex-direction: column;
    }
  }
