.SearchResults{
  width: 50%;
  height: 950px;
  overflow-y: scroll;
  padding: 0.88rem;
  background: linear-gradient(rgba(241, 122, 122, 0.3), rgba(0, 0, 0, 0.7)), url(./Guitar.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  box-shadow: 0 4px 2px 2px #000;
}
::webkit-scrollbar {
    width: 0px;
    background: transparent;
  }

@media only screen and (max-width: 1020px) {
    .SearchResults {
      width: 90%;
      margin-bottom: 2rem
    }
}