.Playlist {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
  width: 36%;
  max-height: 950px;
  padding: 2.27rem 1.16rem;
  background: url(./Guitar.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  box-shadow: 0 4px 2px 2px #000;
}

.Playlist input {
  width: 100%;
  border: 0;
  outline: 0;
  background: transparent;
  border-bottom: 1px solid #1db954;
  font-family: "Montserrat", sans-serif;
  font-size: 1.5rem;
  color: #fff;
}

.Playlist-save {
  cursor: pointer;
  width: 10rem;
  padding: 0.77rem 0;
  border-radius: 54px;
  border-width: 0px;
  margin-top: 1.27rem;
  background-color: #1db954;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
  transition: background-color 0.25s;
  color: #fff;
  font-weight: 500;
  margin-left: 0.5rem;
}

.Playlist-save:hover {
  background-color: rgba(65, 233, 129, 0.7);
}

::webkit-scrollbar {
  width: 0px;
  background: transparent;
}

@media only screen and (max-width: 1020px) {
 .Playlist {
  width: 90%;
 } 
}